.HomePage {
    /* min-height: 35rem; */
    text-align: center;
}

.HomePage-header {
    border-bottom: 1px solid #c3c3c3;
    width: 50%;
    position: relative;
    left: 25%;
    z-index: 1000;
}

.AvatarContainer {
    overflow: hidden;
}

.HomePage-avatar, .AvatarContainer {
    width: 8rem;
    height: 8rem;
    border-radius: 4rem;
}

.HomePage-avatar:hover {
    width: 10rem;
    height: 10rem;
    position: relative;
    left: -1rem;
    top:-1rem;
    border-radius: 5rem;
}

.fab {
    font-size: 2rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 1rem;
}

.Contact-board {
    float: right;
    clear: both;
}

.HomePage-body {
    font-family: '微软雅黑';
    color:#263238;
    text-align: left;
    width: 50%;
    position: relative;
    left: 25%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 3rem 0;
    overflow: hidden;
}

.HomePage-body > h1 {
    margin-top: 2rem;
    -webkit-animation:fallIn 1s ease-in;
            animation:fallIn 1s ease-in;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes fallIn {
    from {
        -webkit-transform:translateX(15rem);
                transform:translateX(15rem);
        opacity: 0;
    }
    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fallIn {
    from {
        -webkit-transform:translateX(15rem);
                transform:translateX(15rem);
        opacity: 0;
    }
    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

#HomePage-body-text-2{
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s
}
#HomePage-body-text-3{
    -webkit-animation-delay: 1s;
            animation-delay: 1s
}
#HomePage-body-text-4{
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s
}
#HomePage-body-text-5{
    -webkit-animation-delay: 2s;
            animation-delay: 2s
}
#HomePage-body-text-6{
    -webkit-animation-delay: 2.5s;
            animation-delay: 2.5s
}
#HomePage-body-text-7{
    -webkit-animation-delay: 3s;
            animation-delay: 3s
}
#HomePage-body-text-8{
    -webkit-animation-delay: 3.5s;
            animation-delay: 3.5s
}
#HomePage-body-text-9{
    -webkit-animation-delay: 4s;
            animation-delay: 4s
}
#HomePage-body-text-10{
    -webkit-animation-delay: 4.5s;
            animation-delay: 4.5s
}
#HomePage-body-text-11{
    -webkit-animation-delay: 5s;
            animation-delay: 5s
}
#HomePage-body-text-12{
    -webkit-animation-delay: 5.5s;
            animation-delay: 5.5s
}
#HomePage-body-text-13{
    -webkit-animation-delay: 6s;
            animation-delay: 6s
}
#HomePage-body-text-14{
    -webkit-animation-delay: 6.5s;
            animation-delay: 6.5s
}

.HomePage-footer {
    width: 100%;
    height: 15rem;
    background-color: #263238;
    color:white;
    position: relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 2rem;
}

.HomePage-footer a {
    color:yellow;
}

#QRCode {
    width: 8rem;
    height: 8rem;
}

.Contact-board > a {
    color:darkblue;
}



@media screen and (max-width: 600px) {
    .HomePage-header {
        width: 90%;
        left: 5%;
    }
    .HomePage-body {
        width: 100%;
        left: 0;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        padding: 5rem;
    }
}


