.Labels {
    width: 100%;
    background-color: white;
}

.Labels-panel {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 1rem;
}

.Labels-panel > ul > li {
    border-bottom: 1px solid #f1f1f1;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 1rem 0;
}

.Labels-panel > ul > li:last-child {
    border: none;
}


